.works{
    background-color: lightgreen;
    display: flex;
    align-items: center;
    justify-content: center;

    .slider{
        height: 500px;
        display: flex;
        position: absolute;
        left:0;
        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item{
                width:50%;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                .left{
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                    }

                    .imgContainer{
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        background-color: rgb(246, 162, 131);
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        img{
                            width: 25px;
                        }
                    }

                    h2{
                        font-size: 20px;
                    }
                    
                    p{
                        font-size: 13px;
                    }

                    span{
                        font-size: 14px;
                        font-weight: 600;
                        text-decoration: underline;
                        cursor: pointer;
                    }
                }

                .right{
                    flex: 8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;

                    img{
                        width: 400px;
                        transform: rotate(-18deg);
                        height: 100%;
                    }
                }
            }
        }
    }
}