@import '../../global.scss';

.menu {
    width: 300px;
    height: 100vh;
    background-color: $mainColor;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all .3s ease-in;
    
    &.active{
        right: 0;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 30px;
        font-weight: 300;
        color: white;
        width: 70%;


        li{
            margin-bottom: 25px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }
            
            .logo{
                height: 35px;
                width: 35px;
                border-radius: 5px;
                flex-direction: row;
                margin-right: 10px;
                

            }

            &:hover{
                font-weight: 500;
            }
        }
    }
}